.image-container {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Adjusted .card-tag to position within the image container */
.lessons-count {

  color: #000000CC;
  display: inline-flex;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;

  border-radius: 10px;
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.05);
}
.challenge-image {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  border-radius: 24px;
}

/* Header with back arrow */
.header-with-back {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
}

.back-button {
  background: none;
  border: none;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
}

.challenge-detail {
  margin-right: 16px;
  margin-bottom: 60px;
}

/* “15 уроков” or the lessons count style */
.lessons-count {
  margin-top: 16px;
  margin-left: 16px;
  /* color: #FFD700; or any color you want */
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 500;
  opacity: 0.9;
}

/* Big title */
.challenge-title-details {
  overflow: hidden;
  color: #FFF;
  text-overflow: ellipsis;
  font-family: Inter, sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: -0.4px;
  padding-left: 16px;
  padding-top: 12px;
}

/* Subtitle under the title */
.course-subtitle {
  padding-left: 16px;
  margin-top: 8px;
  color: #707579;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.23px;
  white-space: pre-line;
}


/* Lessons container */
.lessons-container {
  margin-top: 16px;
  padding-left: 16px;
  /* padding-right: 16px; */
}

.lesson-card {
  background: #191919;
  border-radius: 24px;
  margin-bottom: 12px;
  cursor: pointer;
  box-shadow:
    0px 0px 2px 1px rgba(0, 0, 0, 0.02),
    0px 32px 64px 0px rgba(0, 0, 0, 0.04);
}

.lesson-card-inner {
  display: flex;
  padding: 20px;
  align-items: flex-start;
  gap: 16px;
  border-radius: 24px;
}

.lesson-content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.lesson-title {
  color: #FFF;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: -0.4px;
}

.lesson-subtitle {
  color: #707579;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.23px;
}

/* Hide or remove the old .challenge-progress if not needed */
/*
.challenge-progress {
  display: none;
}
*/

.no-tasks-message {
  margin-left: 20px;
  color: #707579;
}

/* Optional: style your video container similarly */
.video-container {
  margin-top: 16px;
}
.watch-video-button {
  display: inline-block;
  background-color: #007aff;
  color: #fff;
  border: none;
  border-radius: 24px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  margin: 16px;
  cursor: pointer;
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.5); /* semi-transparent overlay */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* on top of everything */
}

.modal-content {
  background: #1e1e1e; /* or #fff if you want a light background */
  color: #fff;
  padding: 24px;
  border-radius: 12px;
  max-width: 300px;
  text-align: left;
}

.modal-content h3 {
  margin-top: 0;
}
.modal-content p {
  margin-bottom: 12px;
}

.modal-content button {
  background-color: #007aff;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 10px 16px;
  cursor: pointer;
}
